export default{
    name:"supplier",
    data(){
        return{
            parentMsg:"供应商选择列表",
            supplierdata:[],
            Number:0,
            param:'',
            scloll:'true',
            supplierId:"",
            supplierName:'',
        }
    },
    beforeRouteLeave(to, from, next) {
        // 设置下一个路由的 meta
       from.query.supplierName = this.supplierName;
       from.query.supplierId = this.supplierId;
    //    to.meta.keepAlive = true;  // 让 A 缓存，即不刷新
       //传值
       next();
      },
    methods: {
    click(e,i){
        this.supplierName=e;
        this.supplierId=i;
        this.$router.back(-1);
    },
        submitHandler(value) {
            this.param=value;
            this.supplierdata=[];
            this.Number="0"
            this.getData();
        },
        getData(){
            this.scloll=false;
            console.log(this.Number)
            let Data={"menuId":"104","pageNumber":this.Number,"pageSize":"10","sortName":"PVD_A1_10","order":"desc","filter":"","param":this.param};
            this.$http.get("/API/baseservice/GetContactUnitList", {params:{data:JSON.stringify(Data)}},{ emulateJSON: true })
            .then(res => {
                for(let i in res.data.List){
                this.supplierdata.push(res.data.List[i]);
                }
                this.scloll=true;
            }
            )
        },
        handleScroll(e){
        let $el = e.target;
        let num=this.Number;
        console.log($el.scrollTop,$el.offsetHeight,$el.scrollHeight)
        if ($el.scrollTop + $el.offsetHeight == $el.scrollHeight) {
            num += 10;
            if (num > this.RecordCount+10) {
            this.$message({
                message: "数据已经全部加载完",
                type: "warning"
            });
            return;
            }
            if (this.scloll) {
                this.Number=num;
                this.getData();
            }
        }
    },
    },
    created(){
        this.getData()
    }
}



